html, body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
      'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-size: 10px;
  
  }
  
  * {
    box-sizing: border-box;
  }

  body {
      position: fixed;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
  }
  
  #root {
    display: flex;
    width: 100%;
    height: 100%;
  }
  
  h1, h2, h3, h4, h5, h6, p {
    margin: 0;
  }
  
  code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
      monospace;
  }
  
  .App {
    display: flex;
    width: 100%;
    height: 100%;
    flex-direction: column;
    align-items: center;
    background: linear-gradient(
      to bottom,
      hsl(226.21, 65.41%, 26.08%) 0%,
      hsl(225.69, 65.78%, 26.24%) 8.1%,
      hsl(224.34, 66.79%, 26.67%) 15.5%,
      hsl(222.53, 68.26%, 27.32%) 22.5%,
      hsl(220.59, 70.04%, 28.12%) 29%,
      hsl(218.72, 72%, 29.01%) 35.3%,
      hsl(217.02, 74.02%, 29.94%) 41.2%,
      hsl(215.53, 76.04%, 30.87%) 47.1%,
      hsl(214.25, 78.01%, 31.76%) 52.9%,
      hsl(213.18, 79.88%, 32.58%) 58.8%,
      hsl(212.28, 81.62%, 33.31%) 64.7%,
      hsl(211.55, 83.19%, 33.93%) 71%,
      hsl(210.97, 84.55%, 34.43%) 77.5%,
      hsl(210.55, 85.62%, 34.79%) 84.5%,
      hsl(210.29, 86.34%, 35.02%) 91.9%,
      hsl(210.19, 86.59%, 35.1%) 100%
    );
}

.icon {
    width: 2rem;
    height: 2rem;
    cursor: pointer;
}
