.form-container {
    display: flex;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    padding-top: 8rem;
    justify-content: center;
    color: #fff;
    background: linear-gradient(
        to bottom,
        hsla(210.19, 86.59%, 35.1%, 0) 0%,
        hsla(210.19, 86.59%, 35.1%, 0.013) 6.6%,
        hsla(210.19, 86.59%, 35.1%, 0.049) 12%,
        hsla(210.19, 86.59%, 35.1%, 0.104) 16.5%,
        hsla(210.19, 86.59%, 35.1%, 0.175) 20.5%,
        hsla(210.19, 86.59%, 35.1%, 0.259) 24.1%,
        hsla(210.19, 86.59%, 35.1%, 0.352) 27.7%,
        hsla(210.19, 86.59%, 35.1%, 0.45) 31.5%,
        hsla(210.19, 86.59%, 35.1%, 0.55) 35.7%,
        hsla(210.19, 86.59%, 35.1%, 0.648) 40.6%,
        hsla(210.19, 86.59%, 35.1%, 0.741) 46.5%,
        hsla(210.19, 86.59%, 35.1%, 0.825) 53.7%,
        hsla(210.19, 86.59%, 35.1%, 0.896) 62.3%,
        hsla(210.19, 86.59%, 35.1%, 0.951) 72.8%,
        hsla(210.19, 86.59%, 35.1%, 0.987) 85.2%,
        hsl(210.19, 86.59%, 35.1%) 100%
      );

      
    pointer-events: none;

    
    form {
        display: flex;
        pointer-events: visible;
        width: 100%;
        max-width: 600px;
        padding: 3.2rem 1.6rem;
        position: relative;

        input {
            display: flex;
            width: 100%;
            height: 5.2rem;
            border-radius: 2.6rem;
            background: rgba($color: #ffffff, $alpha: .05);
            border: none;
            backdrop-filter: blur(20px);
            color: #fff;
            font-size: 1.6rem;
            padding: 1.6rem 3.2rem;

            &::placeholder {
                color: #fff;
                opacity: 0.5;
            }
            &:focus {
                outline: none;
            }
        }

        button {
            position: absolute;
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 2.4rem;
            color: white;
            width: 5.2rem;
            height: 5.2rem;
            background: rgba($color: #ffffff, $alpha: .05);
            top: 3.2rem;
            right: 1.6rem;
            border: none;
        }
    }

}
